import React, { lazy, useEffect } from 'react'
import { createBrowserRouter, RouterProvider} from "react-router-dom";
import Container from './components/main-components/Container'
import StoreContextProvider from './store/store-context';
import Redirect from './pages/Redirect';
import SuspenseComponent from './components/SuspenseComponent'
import { componentLoader } from './utils/utils';
import ProductPage from './pages/ProductPage';
import ProductGallery from './pages/ProductGallery';



const ImportLandingPage = ()=>import('./pages/LandingPage')
const LandingPage = lazy(ImportLandingPage)

const ImportStoreContextProvider = ()=>import('./pages/product-page/store/product-context')
const StoreContentProvider  = lazy(ImportStoreContextProvider )

const router = createBrowserRouter([
  {
    path: "/",
    element: <Container/>,
    children: [
      // {
      //   index: true,
      //   element: <HomePage/>,
      // },
      {
        path: 'redirect',
        element: <Redirect/>,
      },
      // {
      //   path: 'categories/:categoryId',
      //   element: <Outlet/>,
      //   children: [
      //     {
      //       index: true,
      //       element: <CategoryPage/>
      //     },
      //   ]
      // },
      {
        path: 'products/:slug/:id',
        loader: ()=>componentLoader(ImportStoreContextProvider),
        element: <StoreContentProvider />,
        children: [{
          index: true,
          element: <ProductPage/>,
        },
        {
          path: 'gallery',
          element : <ProductGallery/>,
        }
      ]
      },
    ],
  },
  {
    path: 'products/:slug/:id/landing-page',
    loader: ()=>componentLoader(ImportLandingPage),
    element: <SuspenseComponent Component={LandingPage} />
  }
]);

const App = () => {
  return (
    <StoreContextProvider>
      <RouterProvider router={router} />
    </StoreContextProvider>
  )
}

export default App